table {
  width: 100%;
}

table th {
  padding: 0.8em 0;
  text-transform: uppercase;
  opacity: 0.4;
}

table thead,
table td {
  border-bottom: 1px solid var(--gray);
  padding: 0.8em 0;
}

table td:nth-child(2) {
  padding-right: 1em;
}

table tr:last-child td {
  border: none;
}

table a,
table a:hover {
  color: var(--link); 
}
