
.Layout__about p a,
.Layout__about p a:hover {
  color: var(--link); 
}

.Layout__about .Content__inner {
    width: var(--width);
    font-size: 1.2em;
}

.Layout__about table td:not(:first-child) {
}

.Layout__about table ul {
    list-style: none;
    padding: 0;
    margin: 0;
}

.Layout__about table ul li {
    list-style: none;
}

@media all and (max-width: 1024px) {
    .Layout__about .Content__inner,
    .Layout__about {
        width: 100%;
        margin: 0;
    }
}

@media all and (max-width: 640px) {
    .Layout__about .Content__inner,
    .Layout__about {
        width: 100%;
        margin: 0;
    }

    .Layout__about table {
        margin: 0;
    }

    .Layout__about thead {
        display: none;
    }

    .Layout__about tr {
        display: flex;
        flex-direction: column;
        margin: 0 0 1em;
    }

    .Layout__about table td {
        display: block;
        width: 100%;
        border: none;
        padding: 0;
        margin: 0 0 var(--space);
    }

    .Layout__about tr {
        display: flex;
        flex-direction: column;
        margin: 0 0 1em;
    }

    .Layout__about table td:last-child {
        margin: 0;
    }
}
